<template>
  <div id="tmpl" class="hello">
    <div id="searchGroup">
      <el-input size="small" v-model="opeName" class="searchInput" placeholder="/" clearable></el-input>
      <!--<p>{{this.opeName}}</p>-->
      <el-button type="primary" @click="getSearchContent" size="small"></el-button>
      <el-button type="primary" size="small"></el-button>
      <el-popover
          v-model="visible2"
          id="alertDiv"
          tooltip-effect="dark"
          ref="multipleTable"
          placement="bottom"
          width="1200"
          trigger="click">
        <el-input size="small" v-model="opeName2" class="searchInput" style="margin-top:0px;margin-right:8px" placeholder="/" clearable></el-input>
        <el-button type="primary"  @click="getSearchContent2" size="small"></el-button>
        <el-button type="primary" @click="closeClickRow" size="small"></el-button>
        <el-button type="primary" slot="reference" size="small"></el-button>


        <el-table
            @selection-change="changeFun"
            :select-on-indeterminate="selectTrueOrFalse"
            :data="tableData2"
            ref="moviesTable"
            tooltip-effect="dark"
            style="width: 100%">
          <el-table-column :selectable='checkboxInit' type="selection"></el-table-column>
          <el-table-column prop="operationCode" label="ICD9-CM3" show-overflow-tooltip></el-table-column>
          <el-table-column prop="opeationName" label="ICD9-CM3" show-overflow-tooltip></el-table-column>
          <el-table-column prop="operationLevel" label="ICD9-CM3" show-overflow-tooltip></el-table-column>
          <el-table-column prop="stdIndicator" label="" show-overflow-tooltip></el-table-column>
          <el-table-column prop="approved" label="" show-overflow-tooltip></el-table-column>
          <el-table-column prop="createDate" label="" show-overflow-tooltip></el-table-column>
          <el-table-column prop="inputCode" label="" show-overflow-tooltip></el-table-column>
          <el-table-column prop="creator" label="" show-overflow-tooltip></el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
            class="pull-right clearfix"
            @size-change="handleSizeChange2"
            @current-change="handleCurrentChange2"
            :current-page="pageNo2"
            :page-sizes="pageSizesList2"
            :page-size="5"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalDataNumbers2">
          </el-pagination>
        </div>
      </el-popover>
      <el-button type="primary" @click="centerDialogVisible = true" size="small"></el-button>
      <el-dialog
        title=""
        :visible.sync="centerDialogVisible"
        width="30%"
        center>
        <span></span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="centerDialogVisible = false" size="small"> </el-button>
          <el-button type="primary" @click="removeData" size="small"> </el-button>
        </span>
      </el-dialog>
    </div>

    <div>
      <el-table
        @selection-change="changeFun2"
        @cell-click="clickName"
        :data="tableData"
        ref="multipleTable"
        tooltip-effect="dark"
        style="width: 100%">
        <el-table-column type="selection"></el-table-column>
        <el-table-column prop="creator" label="" show-overflow-tooltip>
          <template slot-scope="scope">
            <span style="color:#09f;cursor: pointer">{{ scope.row.creator }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="operationCode" label="" show-overflow-tooltip></el-table-column>
        <el-table-column prop="opeationName" label=""></el-table-column>
        <el-table-column prop="operationLevel" label=""></el-table-column>
        <el-table-column prop="createDate" label="" show-overflow-tooltip></el-table-column>
      </el-table>
      <div class="block">
        <el-pagination
          class="pull-right clearfix"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pageNo"
          :page-sizes="pageSizesList"
          :page-size="10"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalDataNumbers">
        </el-pagination>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  data:function(){
    return {
      //
      selectTrueOrFalse:false,
      visible2:false,
      opeName:'',
      opeName2:'',
      tableData: [],//
      tableData2: [],//
      totalDataNumbers: 0,//,
      totalDataNumbers2: 0,//,
      currentPage1: 1,//1
      currentPage2: 1,//1
      //
      pageNo: 1,//
      pageNo2: 1,//
      pageSize: 10,//
      pageSize2: 5,//
      pageSizesList: [10, 15, 20, 30, 50],//
      pageSizesList2: [3,5,10, 15],//

      //ID
      ids:'',
      ids2:'',
      moviesTable1:[],
      //
      centerDialogVisible:false
    }
  },
  created:function(){
    this.getPageNation();//
    this.getOutPageNation();//

  },
  methods:{
    /*--------------  -------*/
    clickName:function(row, column, cell, event){
      if(column.label == ''){
        //console.log(row.id);
        var operationId = row.id;
        var url = '/api/com/operation/getOperationEducation';
        this.$http.get(url,{params:{opeId:operationId}}).then(function(res){
          if(res.body){
            this.$router.push({name:'doctorChoice',params:{data:res.body}});
          }
        });
      }
    },
    /*handleEdit:function(index, row) {
      this.$router.push('/doctorChoice');  //
    },*/
    /* ----------------  --------------- */
    //id
    changeFun2:function(val){
      if(val.length>0){
        for(var i=0;i<val.length;i++){
          var vals = val[i];
        }
        this.ids2 += vals.id+',';
      }else{
        return
      }
      //console.log(this.ids);
    },
    //
    removeData:function(){
      var ss = this.ids2.split(',');
      // console.log(ss);
      //:ES6
      //var s6 = [...new Set(ss)];
      var res = [];
      var json = {};
      for(var i = 0; i < ss.length; i++){
        if(!json[ss[i]]){
          res.push(ss[i]);
          json[ss[i]] = 1;
        }
      }
      var removeIds = res.join(',');
      //console.log(removeIds);
      var url = '/api/com/operation/deleteOperationById';
      this.$http.get(url,{params:{ids:removeIds}}).then(function(res){
        if(res.body.zt === 'true'){
          //alert('');
          this.getPageNation();
        }else{
          alert('');
          return;
        }
      });
      this.centerDialogVisible = false;
    },
   /* ------------- ------------*/
    //bz0
    checkboxInit:function(row){
      //console.log(row);
      if(row.bz != 0){
        return 0;
      }else{
        return 1;
      }
    },
    //2
    changeFun:function(val){
      if(val.length>0){
        for(var i=0;i<val.length;i++){
          var vals = val[i];
        }
        this.ids += vals.id+',';
      }else{
        return
      }
      //console.log(this.ids);
    },
    //1
    /*clickRow:function(row){
      this.$refs.moviesTable.toggleRowSelection(row);
      if(row.bz != 0){
        return 0;
      }else{
        return 1;
      }
    },*/
    //
    //3(ID)
    closeClickRow:function(row,event){
      //console.log(this.ids);
      //id
      var ss = this.ids.split(',');
     // console.log(ss);
      //:ES6
      //var s6 = [...new Set(ss)];
      var res = [];
      var json = {};
      for(var i = 0; i < ss.length; i++){
        if(!json[ss[i]]){
          res.push(ss[i]);
          json[ss[i]] = 1;
        }
      }
      var idsGruop = res.join(',');
      //console.log(idsGruop);
      //s6 = s6.join(',');
      //idsGroup = s6;
      //console.log(idsGroup);
      var url = '/api/com/operation/inserOperation';
      this.$http.get(url,{params:{ids:idsGruop}}).then(function(res){
        //console.log(res.body.zt);
        //alert(res.zt);
        if(res.body.zt === 'true'){
          this.getPageNation();
          this.ids = '';
          this.visible2 = false;
        }else{
          alert(',');
        }
      });
      //console.log(this.moviesTable);
      //alert('fda');
      this.opeName2 = '';
      this.selectTrueOrFalse = false;
    },


    //input
    getSearchContent:function(){
      this.getPageNation();
    },
    getSearchContent2:function(){
      this.getOutPageNation();
    },

    handleCurrentChange:function(val) {
      this.currentPage1 = val;
      //console.log(val);
      this.getPageNation();
    },
    handleCurrentChange2:function(val2) {
      this.currentPage2 = val2;
      //console.log(val);
      this.getOutPageNation();
    },
    handleSizeChange:function(size) {
      this.pageSize = size;
      //alert('');
      //console.log(size+'ss');
      this.getPageNation();
    },
    handleSizeChange2:function(size2) {
      this.pageSize2 = size2;
      //alert('');
      //console.log(size+'ss');
      this.getOutPageNation();
    },
    /* -------------- ----------------- */
    //
    //
    getPageNation:function(){
      //console.log(this.currentPage1+': ; ',this.pageSize+': ; ',this.opeName+': ')
      var url = '/api/com/operation/getOperationList';
      this.$http.get(//
        url,//
        {params:{name:this.opeName,page:this.currentPage1,size:this.pageSize}}
      ).then(function (res) {//
        //console.log(this.opeName);
        if (res) {//
          //console.log(res.body);
          this.tableData = res.body.tableData;//
          this.totalDataNumbers = res.body.totalDataNumber;
          //console.log(this.tableData);
          this.listLoading=false;
        } else {
          this.tableData = [];
          this.totalDataNumbers = 0;
        }
      })
    },
    //
    getOutPageNation:function() {
      var url = '/api/com/operation/getIcd9ListByName';
      this.$http.get(//
        url,//
        {params:{name:this.opeName2,page:this.currentPage2,size:this.pageSize2}}
      ).then(function (res) {//
          //console.log(this.opeName);
          if (res) {//
            //console.log(res.body);
            this.tableData2 = res.body.tableData;//
            this.totalDataNumbers2 = res.body.totalDataNumber;
            /*if(res.body.tableData.bz == 0){
              console.log('0000')
            }*/
            //console.log(res.body.tableData.bz);
            this.listLoading=false;
          } else {
            this.tableData2 = [];
            this.totalDataNumbers2 = 0;
          }
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #tmpl{
    margin-top: 60px;
  }
.searchInfo{
  width:100%;
  height:62px;
  background-color: red;
}
.searchInput{
  float:left;
  margin-top:8px;
}
#searchGroup{
  height:62px;
}
#alertDiv>button,
#searchGroup>button{
  margin-top:8px;
  margin-left:8px;
  float:left;
}
  .searchInput{
    width:220px;
    height:50px;
    margin-left:0px;
    text-align: left;
  }
#opeTable{
  border-collapse:collapse;
  width:90%;
  margin-left:5%;
}
#opeTable tr td{
  border:1px solid #ccc;
  height:30px;
}
#opeTable tr td:nth-child(1){
  width:30px;
}
.el-main .el-table{
  text-align: left;
}
</style>



