<template>
  <div id="tmpl">
    <div id="searchGroup">
      <el-input v-model="userName" class="inputs" placeholder="" clearable size="small"></el-input>

      <el-button type="primary" size="small" @click="getUserList"></el-button>

      <el-button type="primary" size="small" @click="showBox"></el-button>
      <el-dialog
        @close="closeDialog"
        title=""
        :visible.sync="dialogFormVisible"
        :close-on-press-escape="false">
        <el-form>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="addUserName" auto-complete="off" clearable></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="addPassWord" auto-complete="off" clearable></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="closeDialog"> </el-button>
          <el-button type="primary" @click="addNewUser"> </el-button>
        </div>
      </el-dialog>

      <el-button type="primary" size="small" @click="delRows"></el-button>
      <div class="rightSearchGroup">
        <el-input
          class="treeInput"
          size="small"
          placeholder=""
          v-model="filterText">
        </el-input>
        <el-button type="primary" size="small" @click="getCheckedNodes"></el-button>
        <el-button type="primary" size="small" @click="choiceAll"></el-button>
      </div>
    </div>
    <el-row :gutter="10">
      <el-col :span="16">
        <div class="grid-content bg-purple">
          <el-table
            v-loading="loading"
            ref="multipleTable"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            @cell-click="rowClick"
            @selection-change="handleSelectionChange">
            <el-table-column
              type="selection"
              width="55">
            </el-table-column>
            <el-table-column
              prop="userName"
              label="">
              <template slot-scope="scope">
                <span style="color:#09f;cursor: pointer">{{ scope.row.userName }}</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="passWord"
              label=""
              show-overflow-tooltip>
            </el-table-column>
          </el-table>
          <div style="text-align: center">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="page"
              :page-sizes="[5, 10, 15, 20]"
              :page-size="size"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
            </el-pagination>
          </div>
        </div>
      </el-col>
      <el-col :span="8">
        <div class="grid-content bg-purple">
          <el-tree
            v-loading="loading2"
            :data="treeData"
            show-checkbox
            default-expand-all
            node-key="id"
            ref="tree"
            :props="defaultProps">
          </el-tree>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {
        loading:true,
        loading2:false,
        //
        //
        maxHeight:0,
        //ID
        delIds:'',
        //ID
        rowID:'',
        //
        addUserName:'',
        addPassWord:'',
        formLabelWidth: '120px',
        //
        dialogFormVisible:false,
        //
        userName:'',
        //
        page:1,//
        size:5,//
        total:0,//
        //
        tableData:[],
        //
        rightTableDataList:[],
        //id
        rightTableIds:'',
        //
        rightReturnList:[],
        //
        checkedList:[],//
        filterText: '',
        filterId:0,
        treeData:[],
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      }
    },
    created:function(){
      this.maxHeight = window.innerHeight-80-62-30;
      //
      this.getUserList();
      //
      this.getTreeData();
    },
    methods:{

      //
      choiceAll:function(){
        this.treeClickNum = !this.treeClickNum;
        if(!this.treeClickNum){
          this.$refs.tree.setCheckedNodes([]);
        }else{
          this.$refs.tree.setCheckedNodes(this.treeData);
        }
      },
      //===========================
      getCheckedNodes:function(){
        //console.log(this.delRoleID);
        var arr = this.delIds.split(',');
        //console.log(arr.length,arr,this.delRoleID);
        if(arr.length>2){
          this.$message({
            showClose: true,
            type: 'warning',
            message: ''
          });
        }else if(arr.length<2){
          this.$message({
            showClose: true,
            type: 'warning',
            message: ''
          });
        }else{
          var rids = this.$refs.tree.getCheckedNodes(true,false);
          var rid = '';
          for(var i=0;i<rids.length;i++){
            rid += rids[i].id+',';
          }
          //console.log(arr[0]+' :ID');
          var arrs = arr[0]*1;
          //console.log(arrs);
          //console.log(this.delRoleID+' :ID');
          //console.log(rid+' :ID');
          var url = '/api/com/user/insertUserAndRole';
          this.$http.post(url,{
            uid:arrs,
            ids:rid
          },{emulateJSON:true}).then(function(res){
            //console.log(res);
            if(res.body.zt == 'true'){
              this.$message({
                showClose: true,
                type: 'success',
                message: ''
              });
            }
          });
        }
      },
      //==============================
      //tree
      getTreeData:function(){
        this.loading2 = true;
        var url = '/api/com/user/getAllRole';
        this.$http.post(url,{emulateJSON:true}).then(function(res){
          //console.log(res);
          if(res.body.zt == 'true'){
            //console.log(res);
            this.treeData = res.body.data;
            this.loading2 = false;
          }
        });
      },
      //==========================
      delRows:function(){
        var del = confirm('');
        if(del == true){
          var url = '/api/com/user/deleteUserById';
          this.$http.post(url,{ids:this.delIds},{emulateJSON:true}).then(function(res){
            if(res.body.zt == 'true'){
              this.$message({
                showClose: true,
                type: 'success',
                message: '!'
              });
              //
              this.getUserList();
            }
          });
        }else{
          this.$message({
            showClose: true,
            type: 'warning',
            message: '!'
          });
        }
      },
      //=============================
      closeDialog:function(){
        this.dialogFormVisible = false;
        this.rowID = '';
        //
        this.addUserName = '';
        this.addPassWord = '';
      },
      //ID==============================
      rowClick:function(row, column, cell, event){
        if(column.label == ''){
          //console.log(row);
          this.rowID = row.id;
          //
          this.addUserName = row.userName;
          this.addPassWord = row.passWord;
          this.dialogFormVisible = true;
        }
      },
      //============================
      addNewUser:function(){
        this.dialogFormVisible = false;
        var url = '/api/com/user/insertUser';
        if(this.rowID == ''){
          this.rowID = 0;
        }
        this.$http.post(url,{
          id:this.rowID,
          userName:this.addUserName,
          passWord:this.addPassWord
        },{emulateJSON:true}).then(function(res){
          if(res.body.zt == 'true'){
            if(this.rowID == ''){
              this.$message({
                showClose: true,
                type: 'success',
                message: '!'
              });
            }else{
              this.$message({
                showClose: true,
                type: 'success',
                message: '!'
              });
            }
            //
            this.getUserList();
          }
        });
      },
      //=========================
      showBox:function(){
        this.dialogFormVisible = true;
      },
      //=========================
      getUserList:function(){
        var url = '/api/com/user/getUserList';
        this.$http.get(url,{params:{
          name:this.userName,
          page:this.page,
          size:this.size
        }}).then(function(res){
          //console.log(res);
          if(res.body.zt == 'true'){
            this.tableData = res.body.data;
            this.total = res.body.total;
            //console.log(this.total);
            this.loading = false;
          }
        });
      },
      //element-ui====================
      toggleSelection:function(rows){
        if (rows) {
          rows.forEach(function(row) {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange:function(val) {
        this.loading2 = true;
        //ID==================
        this.multipleSelection = val;
        //console.log(val);
        var ids = '';
        for(var i=0;i<val.length;i++){
          ids += val[i].id+',';
        }
        this.delIds = ids;
        //console.log(ids);


        //=============================
        var lastArr = val[val.length-1];
        //console.log(lastArr);
        if(val==''){
          //console.log('');
          this.filterText = '';
          this.filterId = 0;
          this.$refs.tree.setCheckedKeys([]);
        }else{
          this.$refs.tree.setCheckedKeys([]);
          this.checkedList = [];
          this.filterText = lastArr.userName;
          this.filterId = lastArr.id;
          //console.log(this.filterId);
          //console.log(this.filterText,this.filterId);
          var url = '/api/com/user/getRoleAndUserByUid';
          this.$http.post(url,{uid:this.filterId},{emulateJSON:true}).then(function(res){
            //console.log(res);
            if(res.body.zt == 'true'){
              //this.checkedList = res.body.data;
              this.$refs.tree.setCheckedNodes(res.body.data);
              this.loading2 = false;
            }
          });
        }
      },
      //element-ui=======================
      handleSizeChange:function(val) {
        this.loading2 = false;
       // console.log(` ${val} `);
        this.size = val;
        //
        this.getUserList();
      },
      handleCurrentChange:function(val) {
        this.loading2 = false;
        //console.log(`: ${val}`);
        this.page = val;
        //
        this.getUserList();
      }
    }
  }
</script>

<style scoped>
  #tmpl{
    margin-top: 60px;
  }
  #tmpl{
    text-align: left;
  }
  #searchGroup{
    text-align: left;
    height:62px;
    line-height: 50px;
  }
  .inputs{
    width:150px;
  }
  .treeInput{
    width:150px;
  }
  .rightSearchGroup{
    width:290px;
    height:50px;
    float:right;
  }
  .choiceAll{
    display: inline-block;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    height:48px;
    width:100%;
    border-bottom: 1px solid #ddd;
    padding:14px 0;
  }
  ul{
    margin:0;
    padding:0;
  }
  li{
    box-sizing: border-box;
    height:48px;
    border-bottom:1px solid #ddd;
    padding:14px 0;
  }
</style>



