<template>
  <div id="tmpl">
    <!------------------------------------------------>
    <div class="searchs">
      <span></span>
      <el-date-picker
        class="searchInputs"
        size="small"
        v-model="startTime"
        type="date"
        placeholder="">
      </el-date-picker>
      <span></span>
      <el-date-picker
        class="searchInputs"
        size="small"
        v-model="endTime"
        type="date"
        placeholder="">
      </el-date-picker>
      <el-input
        class="searchInputs"
        size="small"
        placeholder=""
        v-model="searchDoctor"
        clearable>
      </el-input>
      <el-input
        class="searchInputs"
        size="small"
        placeholder=""
        v-model="searchOpe"
        clearable>
      </el-input>
      <el-input
        class="searchInputs"
        size="small"
        placeholder=""
        v-model="searchMan"
        clearable>
      </el-input>
      <el-button size="small" type="primary" @click="getTableData"></el-button>
    </div>

    <!---------------------------------------------------->
    <div class="contentTable">
      <el-table
        :data="tableData3"
        :max-height="tableHeight"
        border
        :header-cell-style="headStyle"
        style="width: 100%">
        <el-table-column
          prop="chiefDoctor"
          label=""
          min-width="10%">
        </el-table-column>
        <el-table-column
          prop="operationName"
          label=""
          min-width="20%">
        </el-table-column>
        <el-table-column
          prop="patientName"
          label=""
          min-width="10%">
        </el-table-column>
        <el-table-column
          prop="operationRoom"
          label=""
          min-width="10%">
        </el-table-column>
        <el-table-column
          prop="operationDate"
          label=""
          min-width="10%">
        </el-table-column>
        <el-table-column
          prop="operationTime"
          label=""
          min-width="10%">
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="1"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {
        tableHeight:0,//
        /*--------------------------------*/
        startTime: '2018-7-24',//
        endTime:'2018-7-24',//
        searchDoctor:'',//
        searchOpe:'',//
        searchMan:'',//
        /*----------------------------------*/
        pageNumbers:'',//
        pageSize:'5',//
        total:0,//
        //
        tableData3: []
      }
    },
    created:function(){
      this.getTableHeight();
      this.getTableData();
    },
    methods:{
      //
      headStyle:function(row, column, rowIndex, columnIndex){
        return 'tableThStyle'
      },
      //
      getTableHeight:function(){
        var innerHeights = window.innerHeight-220;
        this.tableHeight = innerHeights;
        var nowTime = new Date();
        var year = nowTime.getFullYear();
        var month = nowTime.getMonth()+1;
        var day = nowTime.getDate();
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (day >= 0 && day <= 9) {
          day = "0" + day;
        }
        //console.log(year,month,day);
        //this.startTime = year+'-'+month+'-'+day;
        //this.endTime = year+'-'+month+'-'+day;
        //console.log(this.startTime);
      },
      getTableData:function(){
        var url = '/api/com/operationManager/getOperationManagerList';
        this.$http.get(url,{params:{
          sData:this.startTime,
          eData:this.endTime,
          pname:this.searchMan,
          opeName:this.searchOpe,
          doctor:this.searchDoctor,
          page:this.pageNumbers,
          size:this.pageSize}}).then(function(res){
            //console.log(res);
          if(res.body.zt == 'true'){
            this.tableData3 = res.body.data;
            this.total = res.body.total;
          }
        });
      },
      handleSizeChange:function(val) {
        //console.log(` ${val} `);
        this.pageSize = val;
        this.getTableData();
      },
      handleCurrentChange:function(val) {
        //console.log(`: ${val}`);
        this.pageNumbers = val;
        this.getTableData();
      }
    }
  }
</script>

<style scoped>
  #tmpl{
    margin-top: 60px;
  }
.searchs{
  box-sizing: border-box;
  padding:10px 20px;
  text-align: left;
}
.searchInputs{
  width:150px;
}
.tableThStyle{
  text-align: center;
}
</style>



