<!---->
<template>
  <div id="tmpl">
    <div id="searchGroup">
      <el-input size="small" v-model="menuName" class="searchInput" placeholder="" clearable></el-input>
      <el-button type="primary" size="small"></el-button>
      <el-button type="primary" size="small" @click="formShowHide"></el-button>
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.name" auto-complete="off" clearable></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="form.level" placeholder="" @change="getParentMenu">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select @change="selectOne" v-model="form.pName" placeholder="">
              <el-option v-if="form.level == 0" label="" :value="0"></el-option>
              <el-option v-else v-for="(item,index) in pNames" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.url" auto-complete="off" clearable></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.paixu" auto-complete="off" clearable></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="closeDialog"> </el-button>
          <el-button type="primary" @click="addNewData"> </el-button>
        </div>
      </el-dialog>
      <el-button type="primary" size="small" @click="delRow"></el-button>
    </div>
    <el-table
      ref="multipleTable"
      :data="tableData"
      tooltip-effect="dark"
      :highlight-current-row="true"
      style="width: 100%"
      @selection-change="handleSelectionChange"
      @cell-click="clickRow">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        prop="name"
        label="">
        <template slot-scope="scope">
          <span style="color:#09f;cursor: pointer">{{ scope.row.name }}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="level"
        label="">
      </el-table-column>
      <el-table-column
        prop="pName"
        label=""
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="url"
        label=""
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="level"
        label=""
        show-overflow-tooltip>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageNum"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>
  export default {
    data:function(){
        return {
          delID:'',//id
          //ID
          pOptionsId:0,
          //
          clearSelectData:false,
          //
          pNames:[],
          //
          dialogFormVisible:true,//
          form: {
            pName:'',
            name: '',
            level: '',
            pLevel: '',
            url: '',
            paixu: '',
            delivery: false,
            type: [],
            resource: '',
            desc: '',
            id:''//ID
          },//
          formLabelWidth: '120px',//
          //
          menuName:'',//
          //
          total:0,//
          pageSize:5,//
          pageNum:1,//
          //
          tableData:[
            {
            "pName":"",
            "level":0,
            "name":"11",
            "pid":0,
            "id":3,
            "descript":"11",
            "url":"11"
            }
          ],
          multipleSelection: []
        }
    },
    created:function(){
      this.getTableData();
      this.formShowHide();
    },
    mounted:function(){
      this.clearAllForm();
    },
    methods:{
      //ID
      delRow:function(){
        var x = confirm('');
        if(x == true){
          //console.log(this.delID);
          var url = '/api/com/permission/deletePermissionById';
          this.$http.post(url,{ids:this.delID},{emulateJSON:true}).then(function(res){
            //console.log(res);
            if(res.body.zt == 'true'){
              this.getTableData();
            }
          });
        }else{
          //console.log(2222);
        }
      },
      //
      closeDialog:function(){
        this.form.name = '';//
        this.form.level = '';//
        this.form.url = '';//
        this.form.pName = '';//
        this.form.paixu = '';//
        //console.log(this.form);
        //console.log('qqq');
        this.dialogFormVisible = false;
      },
      //
      clearAllForm:function(){
        if(this.dialogFormVisible = false){
          this.form.name = '';//
          this.form.level = '';//
          this.form.pName = '';//
          this.form.url = '';//
          this.form.paixu = '';//
        }
      },
      //
      addNewData:function(){
        //console.log(this.form.level+' :pName');
        if(this.form.name == ''){
          this.$message({
            showClose: true,
            message: '',
            type: 'warning'
          });
          return
        }else if(this.form.level == ''){
          this.$message({
            showClose: true,
            message: '',
            type: 'warning'
          });
          return
        }else if(this.form.pName == null){
          this.$message({
            showClose: true,
            message: '',
            type: 'warning'
          });
          return
        }else{
          var url = '/api/com/permission/insertPermission';
          if(this.form.level == ''){
            this.form.level = 0;
          }
          if(this.form.level == ''){
            this.form.level = 1;
          }
          if(this.form.level == ''){
            this.form.level = 2;
          }
         // if(this.form.id == ''){
            this.form.id = 0;
          //}
          //console.log(this.form.url+' ',this.form.name+', ',this.form.level+', ',this.pOptionsId+'ID, ',this.form.id+'ID');
          this.$http.post(url,{
            id:this.form.id,//id
            name:this.form.name,//
            level:this.form.level,//
            pid:this.pOptionsId,//ID
            url:this.form.url//
          },{emulateJSON:true}).then(function(res){
            //console.log(this.form.name,this.form.level,this.pOptionsId);
            if(res.body.zt == 'true'){
              //console.log(this.form.level);
              this.getTableData();
              this.$message({
                showClose: true,
                message: '',
                type: 'success'
              });
              this.form.name = '';//
              this.form.level = '';//
              this.form.pName = '';//
              this.form.url = '';//
              this.form.paixu = '';//
            }else{
              this.$message({
                showClose: true,
                message: '',
                type: 'success'
              });
            }
          });
          this.dialogFormVisible = false;
        }

      },
      //
      clearSelect:function(){
        //console.log(1111);
        this.clearSelectData = true;
      },
      //
      selectOne:function(value){
        //console.log(value);
        this.pOptionsId = value;
      },
      //
      getParentMenu:function(){
        //this.form.pName = '';
        var url = '/api/com/permission/getPermissionListByLevel';
        var levels = this.form.level;
        //console.log(levels+' levels');
          if(levels == ''){
            levels = 0;
          }
          if(levels == ''){
            levels = 1;
          }
          if(levels == ''){
            levels = 2;
          }
        this.$http.get(url,{params:{
          level:levels
        }}).then(function(res){
          //console.log(res);
          //console.log(levels);
          if(res.body.zt == 'true'){
              this.pNames = res.body.data;
          }
        });
      },
      //
      formShowHide:function(){
        this.dialogFormVisible = !this.dialogFormVisible;
      },
      //
      toggleSelection:function(rows) {
        if (rows) {
          rows.forEach(function(row){
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      //id
      handleSelectionChange:function(val) {
        this.multipleSelection = val;
        //console.log(val[0].id);
        var ids = '';
        for(var i=0;i<val.length;i++){
          ids += val[i].id+',';
        }
        //console.log(ids);
        this.delID = ids;
      },
      //
      handleSizeChange:function(val) {
        //console.log(` ${val} `);
        this.pageSize = val;
        this.getTableData();
      },
      handleCurrentChange:function(val) {
        //console.log(`: ${val}`);
        this.pageNum = val;
        this.getTableData();
      },
      //
      getTableData:function(){
        var url = '/api/com/permission/getPermissionList';
        //console.log(this.menuName,this.pageNum,this.pageSize);
        this.$http.post(url,{
          name:this.menuName,
          page:this.pageNum,
          size:this.pageSize
        },{emulateJSON:true}).then(function(res){
          if(res.body.zt === 'true'){
            this.total = res.body.total;
            this.tableData = res.body.data;

            for(var i=0;i<this.tableData.length;i++){
              //console.log(this.tableData[i].level)
              if(this.tableData[i].level == 0){
                this.tableData[i].level = ''
              }else if(this.tableData[i].level == 1){
                this.tableData[i].level = ''
              }else if(this.tableData[i].level == 2){
                this.tableData[i].level = ''
              }
            }
            //console.log(this.tableData.level);
          }

        });
      },
      //
      clickRow:function(row, column, cell, event) {
        this.form.id = row.id;
        //console.log(column);
        if(column.label == ''){
        var url = '/api/com/permission/getPermissionById';
        this.$http.post(url, {id: row.id}, {emulateJSON: true}).then(function (res) {
          //console.log(row);
          //console.log(res);
          if (res.body.zt == 'true') {
            if (res.body.data.level == 0) {
              res.body.data.level = '';
            } else if (res.body.data.level == 1) {
              res.body.data.level = '';
            } else {
              res.body.data.level = '';
            }
            //console.log(res);
            this.form = res.body.data;
            this.pOptionsId = res.body.data.pid;

            //
            this.getParentMenu();
            //this.getTableData();
            this.dialogFormVisible = true;
          }
        });
      }
      }
    }
  }
</script>

<style scoped>
#tmpl{
  margin-top: 60px;
}
#searchGroup{
  text-align: left;
  height:62px;
  line-height: 50px;
}
  .searchInput{
    width:150px;
  }
.el-select,
.el-form .el-input{
  width:50%;
}
</style>



