<!---->
<template>
  <div id="tmpl">
    <div id="searchGroup">
      <el-input v-model="roleName" class="inputs" placeholder="" clearable size="small"></el-input>
      <el-button type="primary" size="small" @click="searchRole"></el-button>
      <el-button type="primary" size="small" @click="dialogShowOrHide"></el-button>
      <div style="float:right">
        <el-input
          class="treeInput"
          size="small"
          placeholder=""
          v-model="filterText">
        </el-input>
        <el-button type="primary" size="small" @click="getCheckedNodes"></el-button>
        <el-button type="primary" size="small" @click="choiceAll"></el-button>
      </div>
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="boxRoleName" auto-complete="off"></el-input>
          </el-form-item>
          <!--<el-form-item label="" :label-width="formLabelWidth">
            <el-radio v-model="radioChoice" label="1"></el-radio>
            <el-radio v-model="radioChoice" label="2"></el-radio>
          </el-form-item>-->
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}"
              placeholder=""
              v-model="textareas">
            </el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="addNewRole"> </el-button>
        </div>
      </el-dialog>
      <el-button type="primary" size="small" @click="delRoleRow"></el-button>
    </div>
    <el-row :gutter="10">
      <el-col :span="16">
        <div class="grid-content bg-purple">
          <el-table
            ref="multipleTable"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            @cell-click="clickRow"
            @selection-change="handleSelectionChange">
            <el-table-column
              type="selection"
              width="55">
            </el-table-column>
            <el-table-column
              prop="name"
              label="">
              <template slot-scope="scope">
                <span style="color:#09f;cursor: pointer">{{ scope.row.name }}</span>
              </template>
            </el-table-column>
            <!--<el-table-column
              prop=""
              label="">-->
            </el-table-column>
            <el-table-column
              prop="descript"
              label=""
              show-overflow-tooltip>
            </el-table-column>
          </el-table>
          <div style="text-align: center">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="page"
              :page-sizes="[5, 10, 15, 20]"
              :page-size="size"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
            </el-pagination>
          </div>
        </div>
      </el-col>
      <el-col :span="8">
        <div class="grid-content bg-purple">

          <el-tree
            v-loading="loading"
            :data="treeData"
            show-checkbox
            default-expand-all
            node-key="id"
            ref="tree"
            :props="defaultProps">
          </el-tree>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {
        //loading
        loading:false,
        //ID
        delRoleID:'',
        //ID
        roleId:'',
        //
        boxRoleName:'',//input
        textareas:'',//
        //radioChoice:'1',//
        //
        dialogFormVisible: false,
        formLabelWidth: '120px',
        //
        roleName:'',
        //
        tableData:[],
        //
        page:1,
        size:5,
        total:0,
        //
        checkedList:[],//
        filterText: '',
        filterId:0,
        treeData:[],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        //
        treeClickNum:false
      }
    },
    created:function(){
      //
      this.getData();
      //
      this.getTreeData();
    },
    methods:{
      //
      choiceAll:function(){
        this.treeClickNum = !this.treeClickNum;
        if(!this.treeClickNum){
          this.$refs.tree.setCheckedNodes([]);
        }else{
          this.$refs.tree.setCheckedNodes(this.treeData);
        }
      },
      //
      getCheckedNodes:function(){
        //console.log(this.delRoleID);
        var arr = this.delRoleID.split(',');
        //console.log(arr.length,arr,this.delRoleID);
        if(arr.length>2){
          this.$message({
            showClose: true,
            type: 'warning',
            message: ''
          });
        }else if(arr.length<2){
          this.$message({
            showClose: true,
            type: 'warning',
            message: ''
          });
        }else{
          var rids = this.$refs.tree.getCheckedNodes(true,false);
          var rid = '';
          for(var i=0;i<rids.length;i++){
            rid += rids[i].id+',';
          }
          //console.log(arr[0]+' :ID');
          var arrs = arr[0]*1;
          //console.log(arrs);
          //console.log(this.delRoleID+' :ID');
          //console.log(rid+' :ID');
          var url = '/api/com/role/insertRoleAndPermission';
          this.$http.post(url,{
            rid:arrs,
            ids:rid
          },{emulateJSON:true}).then(function(res){
              if(res.body.zt == 'true'){
                this.$message({
                  showClose: true,
                  type: 'success',
                  message: ''
                });
              }
          });
        }
      },
      //tree
      getTreeData:function(){
        this.loading = true;
        var url = '/api/com/role/getPermissionTree';
        this.$http.post(url,{emulateJSON:true}).then(function(res){
          //console.log(res);
          if(res.body.zt == 'true'){
            //console.log(res);
            this.loading = false;
            this.treeData = res.body.data;
          }
        });
      },
      //
      delRoleRow:function(){
        if(this.delRoleID == ''){
          this.$message({
            showClose: true,
            type: 'warning',
            message: '!'
          });
        }else{
          var sure = confirm('');
          if(sure == true){
            var url = '/api/com/role/deleteRoleById';
            this.$http.post(url,{ids:this.delRoleID},{emulateJSON:true}).then(function(res){
              this.$message({
                showClose: true,
                type: 'success',
                message: '!'
              });
              //
              this.getData();
            });
          }else{
            this.$message({
              showClose: true,
              type: 'warning',
              message: '!'
            });
          }
        }
      },
      //
      addNewRole:function(){
        this.dialogFormVisible = false;
        if(this.roleId == ''){
          this.roleId = 0;
        }
        var url = '/api/com/role/insertRole';
        this.$http.post(url,{
          id:this.roleId,
          name:this.boxRoleName,
          descript:this.textareas
        },{emulateJSON:true}).then(function(res){
            //console.log(res);
            if(res.body.zt == 'true'){
              if(this.roleId == 0){
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                });
              }else{this.$message({
                showClose: true,
                message: '',
                type: 'success'
              });
              }
              //
              this.getData();
            }
        });
      },
      //
      dialogShowOrHide:function(){
        this.dialogFormVisible = !this.dialogFormVisible;
      },
      //
      searchRole:function(){
        //console.log(this.roleName);
        //
        this.getData();
      },
      //
      getData:function(){
        var url = '/api/com/role/getRoleList';
        this.$http.get(url,{params:{
          page:this.page,
          size:this.size,
          name:this.roleName
        }}).then(function(res){
          //console.log(res);
          if(res.body.zt == 'true'){
            this.total = res.body.total;
            this.tableData = res.body.data;
          }
        });
      },
      //element-ui
      //ID,
      clickRow:function(row, column, cell, event){
        //console.log(row);
        if(column.label == ''){
          this.dialogFormVisible = true;
          this.roleId = row.id;
          this.boxRoleName = row.name;//input
          this.textareas = row.descript;//
          //console.log(this.roleId);
        }
      },
      toggleSelection:function(rows) {
        if (rows) {
          rows.forEach(function(){
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange:function(val) {
        //this.loading = false;
        this.multipleSelection = val;
        //console.log(val);
        //ID
        var ids = '';
        for(var i=0;i<val.length;i++){
          ids += val[i].id+',';
        }
        //console.log(ids);
        this.delRoleID = ids;
        //console.log(val);

        //nameid--/
        var lastArr = val[val.length-1];
        if(val==''){
          //console.log('');
          this.filterText = '';
          this.filterId = 0;
          this.$refs.tree.setCheckedKeys([]);
        }else{
          this.$refs.tree.setCheckedKeys([]);
          this.checkedList = [];
          this.filterText = lastArr.name;
          this.filterId = lastArr.id;
          //console.log(this.filterText,this.filterId);
          var url = '/api/com/role/getChildPermission';
          this.$http.post(url,{rid:this.filterId},{emulateJSON:true}).then(function(res){
            this.loading = true;
            if(res.body.zt == 'true'){
              this.loading = false;
              //this.checkedList = res.body.data;
              this.$refs.tree.setCheckedNodes(res.body.data);
            }
          });
        }
      },
      //element-ui
      handleSizeChange:function(val) {
        //console.log(` ${val} `);
        this.size = val;
        //
        this.getData();
      },
      handleCurrentChange:function(val) {
        //console.log(`: ${val}`);
        this.page = val;
        //
        this.getData();
      }
    }
  }
</script>

<style scoped>
  #tmpl{
    margin-top: 60px;
  }
  #tmpl{
    text-align: left;
  }
  #searchGroup{
    text-align: left;
    height:62px;
    line-height: 50px;
  }
  .treeInput,
  .inputs{
    width:150px;
  }
</style>



