<!---->
<template>
  <div id="tmpl">
    <div class="searchGroup">
      <el-input size="small" v-model="opeName" class="searchInput" placeholder="" clearable></el-input>
      <el-button type="primary" size="small" @click="searchClick"></el-button>
      <div class="rightBtns">
        <el-button type="primary" size="small" @click="getOpe"></el-button>
        <el-button type="primary" size="small" @click="noGetOpe"></el-button>
        <el-button type="primary" size="small" @click="getOther"></el-button>
        <el-button type="primary" size="small" @click="refreshTable"></el-button>
      </div>
    </div>

<!--  --------     ----------  -->

    <el-table
      @cell-click="clickCell"
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        fixed
        align="center"
        prop="patientName"
        min-width="50px"
        label="">
        <template slot-scope="scope">
          <span style="color:#09f;cursor: pointer">{{ scope.row.patientName }}</span>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="age"
        min-width="50px"
        label="">
      </el-table-column>
      <el-table-column
        align="center"
        prop="operationName"
        min-width="150px"
        label="">
      </el-table-column>
      <el-table-column
        align="center"
        prop="operationType"
        min-width="80px"
        label="">
      </el-table-column>
      <el-table-column
        align="center"
        prop="evaluateTime"
        min-width="80px"
        label="">
      </el-table-column>
      <el-table-column
        fixed="right"
        min-width="100px"
        align="center"
        prop=""
        label="">
        <template slot-scope="scope">
          <span class="groupsBtn"></span>
          <span class="groupsBtn"></span>
          <span class="groupsBtn"></span>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>

    <!--  ------------------    ----------------  -->
    <el-dialog class="tanchuBox" title="" :visible.sync="dialogFormVisible" @close="outForm">
      <div>
        <el-form :model="form">
          <el-form-item label="" :label-width="formLabelWidth">
            <ul>
              <li>
                <b></b><span style="margin-right:20px" v-text="form.patientName"></span>
                <b></b><span v-text="form.patientAge"></span>
              </li>
              <li>
                <b></b><span v-text="form.opeNames"></span>
              </li>
            </ul>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-radio v-model="form.radios" label="0"></el-radio>
            <el-radio v-model="form.radios" label="1"></el-radio>
            <el-radio v-model="form.radios" label="2"></el-radio>
          </el-form-item>

          <el-form-item label="" :label-width="formLabelWidth">
            <el-checkbox-group v-model="form.labels" @change="handleCheck">
              <el-checkbox class="checkboxList" v-for="item in form.fastList" :key="item.id" :label="item.mid">{{ item.message }}</el-checkbox>
              <br/>
            </el-checkbox-group>
          </el-form-item>

          <el-form-item label="" :label-width="formLabelWidth">
            <el-input
              class="tanchuTextarea"
              type="textarea"
              :rows="2"
              placeholder=""
              v-model="form.textarea">
            </el-input>
            <el-button type="primary" size="small" @click="saveDialog"></el-button>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <div class="liuyanMessage">
              <ul>
                <!--<li v-if=""></li>-->
                <li class="liuyanLi" v-for="item in form.liuyanList" :key="item.id">
                  <span class="messageList" style="" :title="item.message">{{ item.message }}</span>
                  <span>{{ item.messageDate }}</span>
                </li>
              </ul>
            </div>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="outForm" size="small"></el-button>
        <!--<el-button type="primary" @click="dialogFormVisible = false" size="small"> </el-button>-->
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {
        //
        patientId:0,//ID
        dialogFormVisible: false,
        form: {
          //
          patientName:'',//
          patientAge:0,//
          opeNames:'',//
          radios:'',//
          liuyanList:[],//
          fastList:[],//
          labels:[],//value
          strLabels:'',//value


          textarea:'',
          checkList:[],
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        formLabelWidth: '120px',


        //
        opeName:'',
        //
        tableData: [],
        //
        currentPage:1,//
        size:5,//
        total:0,//,
        nums:3
      }
    },
    created:function(){
      //
      this.getTableList();
    },
    mounted:function(){},
    methods:{
      getOpe:function(){
        this.nums = 0;
        this.getTableList();
      },
      noGetOpe:function(){
        this.nums = 1;
        this.getTableList();
      },
      getOther:function(){
        this.nums = 2;
        this.getTableList();
      },
      refreshTable:function(){
        this.nums = 3;
        this.getTableList();
      },
      outForm:function(){
          this.dialogFormVisible = false;
          //form
          this.form.textarea = '';
          this.form.labels = [];
          this.form.radios = '';
      },
      //id
      handleCheck:function(val){
        //console.log(val);
        var ids = '';
        for(var i=0;i<val.length;i++){
          ids += val[i]+',';
        }
        //console.log(ids);
        this.form.strLabels = ids
      },
      //
      saveDialog:function(){
        //console.log('fdsa');
        //console.log(this.form.textarea);
        var url = '/api/com/evaluate/insertPatientEvaluate';
        this.$http.post(url,{
          operationType:this.form.radios,
          id:this.patientId,
          fastMessage:this.form.strLabels,
          message:this.form.textarea
        },{emulateJSON:true}).then(function(res){
          //console.log(res);
          //this.form.liuyanList = res.body.data;
          if(res.body.zt == 'true'){
            this.form.liuyanList = res.body.data;
            this.$message({
              showClose: true,
              message: '',
              type: 'success'
            });

            //form
            this.form.textarea = '';
            this.form.labels = [];
            this.form.radios = '';
            //console.log(this.form);
          }else{
            this.$message({
              showClose: true,
              message: '',
              type: 'error'
            });
          }
        });
      },


      //---------------------------
      //
      clickCell:function(row, column, cell, event){
        this.patientId = row.id;
        //console.log(this.patientId);
        //console.log(column.label);
        if(column.label == ''){
          var url = '/api/com/evaluate/getPatientEvaluateById';
          this.$http.post(url,{
            id:row.id,
            name:row.patientName
          },{emulateJSON:true}).then(function(res){
            if(res.body.zt == 'true'){
              this.dialogFormVisible = true;
              //console.log(res);
              this.form.patientName = res.body.data.patientName;//
              this.form.patientAge = res.body.data.age;//
              this.form.opeNames = res.body.data.operationName;//
              this.form.radios = res.body.data.operationType+'';//
              this.form.liuyanList = res.body.data.leaveMessage;//
              this.form.fastList = res.body.data.fastMessage;//
             // console.log(this.form.fastList);
            }
          });
        }
      },


      //---------------------------------
      //
      searchClick:function(){
        this.getTableList();
      },
      //
      getTableList:function(){
        //console.log(this.opeName);
        var url = '/api/com/evaluate/getPatientEvaluateList';
        this.$http.post(url,{
          page:this.currentPage,
          size:this.size,
          name:this.opeName,
          operationType:this.nums
        },{emulateJSON:true}).then(function(res){
            console.log(res);
          if(res.body.zt == 'true'){
            this.tableData = res.body.data;
            this.total = res.body.total;
          }
        });
      },
      //
      handleSizeChange:function(val) {
        //console.log(` ${val} `);
        this.size = val;
        //
        this.getTableList();
      },
      handleCurrentChange:function(val) {
        //console.log(`: ${val}`);
        this.currentPage = val;
        //
        this.getTableList();
      }
    }
  }
</script>

<style scoped>
  #tmpl{
    margin-top: 60px;
  }
  .checkboxList{
    margin:0;
  }
  .liuyanLi{
    height:40px;
    line-height: 40px;;
  }
  .liuyanLi span{
    display: inline-block;
    height:40px;
    line-height: 40px;
    vertical-align: middle;
  }
  .messageList{
    display: inline-block;
    margin-right:20px;
    width:60%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  .liuyanMessage{
    max-height:150px;
    overflow: auto;
  }
  .groupsBtn{
    background-color: #09f;
    color:#fff;
    cursor:pointer;
  }

.tanchuTextarea{
  width:80%;
}
.el-dialog__header{
  display: none;
}
.searchGroup{
  text-align: left;
  height:62px;
}
.searchInput{
  width:150px;
  margin-top:8px;
}
.rightBtns{
  float:right;
  margin-top:8px;
}
.tanchuBox{
  text-align: left;
}
</style>



